export default function LeagueTable({ teams }) {
  return (
    <table>
      <thead>
        <tr>
          <th>מקום</th>
          <th>קבוצה</th>
          <th>משחקים</th>
          <th>נקודות</th>
        </tr>
      </thead>
      <tbody>
        {teams.map((t, i) => {
          return (
            <tr key={t.key}>
              <td>{i + 1}</td>
              <td>{t.displayName}</td>
              <td className={` ${t.games === 37 ? "done" : ""} `}>{t.games}</td>
              <td>{t.points}</td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
}
